* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #f6f6f6;
}

html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  margin: 0;
  padding: 0;
}
