.loading-content {
  &__wrapper {
    max-width: 4000px;
    display: grid;
    gap: 32px;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 100px;
  }

  &__image {
    width: 100%;
  }

  &__block {
    margin-bottom: 56px;
  }
}

@media screen and (max-width: $ipad-screen) {
  .loading-content {
    display: none;
  }
}